header {
	width: 100%;
	height: 80px;
	padding: 0px 5vw;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;

  &.main {
    background: #fff;
    border-bottom: 1px solid #ccc;
    
    h1 a {
			color: #333;
		}
		.gnb {
			li a {
				color: #555;
			}
		}
		.menu .fa-bars {
			color: #555;
		}
  }

  &.sub {
    background: #111;
    border-bottom: 1px solid #fff;
    
    h1 a {
			color: #fff;
		}
		.gnb {
			li a {
				color: #fff;
			}
		}
		.menu .fa-bars {
			color: #fff;
		}
  }
	
	h1 a {
		font: bold 24px/1 'arial';
	}

	.gnb {
		display: flex;
		align-items: center;

		li {
			margin: 0px 20px;

			a {
				font: 16px/1 'arial';
			}
		}
	}
	.menu {
    display: none;
		.fa-bars {
			display: block;
			font-size: 20px;
      color: #fff
		}
	}
}

@media screen and (max-width: 899px) {
	header {
    .gnb {
      display: none;
    }
		.menu {
			display: block;
		}

	}
}